$block-size: 100px;
$block-gap: 1px;
$calendar-padding: 10px;

$gap-size-sum: $block-gap + $block-size;
$max-width: $block-size * 7 + $block-gap * 6 + $calendar_padding * 2;
$calendar-first-day-margin: $block-gap + $block-size;

$transition-def: 0.5s;

$days:
  0 "mon",
  1 "tue",
  2 "wed",
  3 "thu",
  4 "fri",
  5 "sat",
  6 "sun";
